import React from "react"
import Header from "../components/header"
import Layout from "../components/layout"
import {graphql} from "gatsby"

import styles from "./projects.module.css"

export default function Projects({data}) {

  const projectsSection = data.allMarkdownRemark.edges.map(({node}) => {
    return (
      <div>
        <h2 className={styles.projectTitle}>{node.frontmatter.title}</h2>
        <p>{node.excerpt}</p>
      </div>
    )
  })


  return (
    <Layout>
      <Header headerText="Discover some of my projects"/>
      <p>Here is a selection of some projects I have come to realize. For some of them, more documentation is available under the github link !</p>
      <p className={styles.footnote}>Number of projects: {data.allMarkdownRemark.totalCount}</p>
      {projectsSection}
    </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
          }
          excerpt
        }
      }
    }
  }
  `